.footer {
  background-color: #ffffff;
  padding: 80px 0;
  text-align: center;
}

.footer_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer_text {
  margin-bottom: 50px;
}
.email_link:hover {
  cursor: pointer;
  transition: color 0.3s ease;
  color: #808080;
}
.social_icons {
  display: flex;
  justify-content: center;
}

.icon_link {
  margin: 0 30px;
  color: #000000;
  transition: color 0.3s ease;
}

.icon_link:hover {
  color: #808080;
}

.icon {
  font-size: 25px;
}
